<template>
    <div>
         <div class="row row justify-content-center">
             <div class="col-10">
                 <div class="bg-white p-3 shadow text-sm text-center rounded-lg">
                     JADWAL SHOLAT
                     <br>
                      {{datanya.lokasi}}, 
                      {{datanya.daerah}}
                 </div>
             </div>
         </div>
            <div class="row justify-content-center"   v-for="(item, index) in td" :key="index+'jadwal2'">
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Imsak
                        {{item.imsak}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Subuh
                        {{item.subuh}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Terbit
                        {{item.terbit}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Dhuha
                        {{item.dhuha}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Dzuhur
                        {{item.dzuhur}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Ashar
                        {{item.ashar}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Maghrib
                        {{item.maghrib}}
                    </div>
                </div>
                <div class="col-4 col-sm-3">
                    <div class="shadow p-3 rounded-lg text-center mt-2 text-xs sm:text-sm bg-white font-bold">
                        <!-- <img src="https://undangan.officialapp.website/files/islamic.png" style="display:block;margin:auto;width:50px;" alt=""> -->
                        <i class="fas fa-star-and-crescent text-lg" ></i>
                        Isya
                        {{item.isya}}
                    </div>
                </div>
                <div class="col-12">
                    <hr>
                </div>
            </div>
    </div>
</template>
<script>
import axios from 'axios'
import datefns from 'date-fns'
import listkota from '@/static/json/id_kota_sholat.json'
// import libur from '@/static/json/calendar_libur.json'
export default{
    data(){
        return{
            datanya:[],
            jadwal:[],
            vdata:{},
            lokasi:[],
            lokasinya:'',
            now:'',
            jam:'',
            cekantara:'subuh'
        }
    },
    mounted(){
        this.getData()
        // this.getLokasi();
        if(localStorage.getItem('lokasinya')){
            this.lokasinya = localStorage.getItem('lokasinya');
        }else{
            this.lokasinya=this.$store.state.lokasinya
        }
        this.$forceUpdate();
        let tanggal = new Date();
        this.jam=parseInt(tanggal.toString().split(' ')[4].split(':')[0]);
        this.lokasi = listkota;
    },
    watch:{
        lokasinya(){
            this.getData()
        }
    },
    computed:{
        td(){
            let that =this;
            let data = this.jadwal;
            data=data.filter(e=>{
                if(e.date==that.now){
                    return e
                }
            })
            let cekantara='';
            let antara=data.filter(e=>{
                console.log(that.jam,parseInt(e.maghrib.split(':')[0]))
                console.log(that.jam,parseInt(e.isya.split(':')[0]))
                console.log(that.jam > parseInt(e.maghrib.split(':')[0]))
                console.log(that.jam < parseInt(e.isya.split(':')[0]))
                if(that.jam >= parseInt(e.subuh.split(':')[0]) && that.jam <= parseInt(e.dzuhur.split(':')[0])){
                    cekantara='dzuhur'
                    return e
                }else if(that.jam >= parseInt(e.dzuhur.split(':')[0]) && that.jam <= parseInt(e.ashar.split(':')[0])){
                    cekantara='ashar'
                    return e
                }else if(that.jam >= parseInt(e.ashar.split(':')[0]) && that.jam <= parseInt(e.maghrib.split(':')[0])){
                    cekantara='maghrib'
                    return e
                }else if(that.jam >= parseInt(e.maghrib.split(':')[0]) && that.jam <= parseInt(e.isya.split(':')[0])){
                    cekantara='isya'
                    return e
                }else{
                    cekantara='subuh'
                }
            })
            this.cekantara=cekantara
            console.log(cekantara)
            this.$forceUpdate()
            return data
        }
    },
    methods: {
        getData(){
            let that=this;
            let tanggal = new Date();
            this.vdata.bulan = this.getTgl(tanggal.toString().split(' ')[1]);
            this.vdata.tahun = tanggal.toString().split(' ')[3];
            this.now=datefns.format(
                    new Date(),
                    "YYYY-MM-DD"
                )
            navigator.geolocation.getCurrentPosition(successFunction, ()=>{});
            function successFunction(position) {
                var lat = position.coords.latitude;
                var lng = position.coords.longitude;
                console.log(lat, lng)
                axios.get(`https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${lat}&longitude=${lng}&localityLanguage=en`).then(res=>{
                    let cekLokasi=that.lokasi.filter(e=>e.lokasi.toLowerCase().indexOf(res.data.locality.toLowerCase())!=-1)
                    that.lokasinya=cekLokasi.length>0?cekLokasi[0].id:'2115'
                    axios.get(`https://api.myquran.com/v1/sholat/jadwal/${that.lokasinya}/${that.vdata.tahun}/${that.vdata.bulan}`).then(res=>{
                        console.log(res)
                        that.datanya=res.data.data;
                        that.jadwal=res.data.data.jadwal;
                        that.$forceUpdate()
                    })
                });
            }
            
        },
        getLokasi(){
            axios.get('https://api.myquran.com/v1/sholat/kota/semua').then(res=>{
                this.lokasi = res.data
            })
        },
        getTgl(data){
            let hasil='';
            if(data=='Jan') {
                hasil=1;
            }
            else if(data=='Feb') {
                hasil=2;
            }
            else if(data=='Mar') {
                hasil=3;
            }
            else if(data=='Apr') {
                hasil=4;
            }
            else if(data=='May') {
                hasil=5;
            }
            else if(data=='Jun') {
                hasil=6;
            }
            else if(data=='Jul') {
                hasil=7;
            }
            else if(data=='Aug') {
                hasil=8;
            }
            else if(data=='Sep') {
                hasil=9;
            }
            else if(data=='Oct') {
                hasil=10;
            }
            else if(data=='Nov') {
                hasil=11;
            }
            else if(data=='Dec') {
                hasil=12;
            }
            return hasil;
        },
    },
    layout:'app_islamic'
}
</script>